.nav{
    position: fixed;
    top: 0;
    padding: 20px;
    width: 100%;
    height: 30px;
    z-index: 1;

/*----------Animation----------*/
transition-timing-function: ease-in;
transform: all 0.5s;
}

.nav__contents{
    display: flex;
    justify-content: space-between;
}
.nav_logo{
    position: fixed;
    top: 10px;
    left: 0;
    width: 80px;
    object-fit: contain;
    padding-left: 20px;
    cursor: pointer;
}
.nav_avatar{
    position: fixed;
    right: 20px;
    cursor: pointer;
    width: 30px;
}
.nav__black{
    background-color: #111;
    color: #111;
}